.nav {
  z-index: 2;
}
.nav-item {
  color: #484848;
  font-family: "Consolas", "Monaco", "Menlo", monospace;
  font-size: 1.1em;
  padding-right: 1em;
  text-decoration: none;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.nav-item:hover,
.nav-item.active {
  border: none;
  cursor: pointer;
  color: #000;
}
.nav .friday-message {
  color: #008000;
  vertical-align: super;
  font-size: 0.8em;
}
.page-turn {
  cursor: pointer;
  z-index: 12;
  opacity: 1;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
@media screen and (max-width: 600px) {
  .page-turn {
    opacity: 0;
  }
}
.right {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}
.up {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}
.down {
  -webkit-transform: rotate(270deg);
      -ms-transform: rotate(270deg);
          transform: rotate(270deg);
}
.content {
  padding: 0;
  white-space: pre;
  font-size: 1.3rem;
}
.content p {
  font-size: 1em;
}
.content p.text {
  line-height: 1.5;
  margin-bottom: 15px;
  white-space: pre;
}
.content p.text:first-line {
  font-variant: small-caps;
  letter-spacing: 1px;
}
.content p.text:last-child {
  margin-bottom: 30px;
  padding-bottom: 30px;
}
.author {
  font-variant: small-caps;
  letter-spacing: 1px;
  padding-bottom: 3em;
}
.no-content {
  font-size: 1em;
  font-variant: small-caps;
  position: absolute;
  top: 41%;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.text-area__wrapper {
  height: 100%;
  padding-bottom: 2em;
}
.text-area__content,
.text-area__content[type=text] {
  height: 100%;
  border: 1px solid #ccc;
  border-radius: 3px;
  line-height: 1.5;
  font-size: 1.3rem;
  white-space: pre;
}
.text-area__content:first-line,
.text-area__content[type=text]:first-line {
  font-variant: small-caps;
  letter-spacing: 1px;
}
.text-area__content:focus,
.text-area__content[type=text]:focus {
  border: 1px solid #000;
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.info-page {
  margin: auto;
  padding: 2em;
}
.origin-story {
  color: #000;
  padding-left: 2em;
  padding-right: 2em;
  padding-bottom: 2em;
}
.origin-story p {
  padding-top: 0.5em;
}
.origin-image {
  width: 100%;
}
.github-link {
  padding-top: 2em;
  padding-bottom: 2em;
  text-align: center;
}
.github-link a {
  text-decoration: none;
}
.home {
  max-width: 800px;
  padding-left: 5%;
  padding-right: 5%;
  width: 100%;
}
.left {
  position: fixed;
  top: 40%;
  left: 5%;
}
.right {
  position: fixed;
  top: 40%;
  right: 5%;
}
.home .poem {
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  position: absolute;
  margin: auto;
  z-index: 0;
}
.home .poem .content {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
      -ms-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  max-height: 60%;
  max-width: 95%;
}
.left-half,
.right-half {
  top: 0;
  position: fixed;
  width: 15%;
  height: 100%;
  opacity: 0.1;
  z-index: 1;
}
.left-half {
  left: 0;
}
.right-half {
  right: 0;
}
.typewriter {
  padding: 2em;
}
.typewriter__content {
  padding-bottom: 2em;
  text-align: left;
}
.typewriter__submit {
  border-radius: 2px;
  font-size: 0.9em;
  padding: 0.3em;
}
.typewriter .poem {
  height: 100%;
  left: 0;
  margin: auto;
  position: relative;
  text-align: center;
  top: 1em;
  width: 100%;
  z-index: 1;
}
.typewriter .poem .content {
  display: inline-block;
  text-align: left;
}
html,
body {
  background: #f5f5f5;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.container {
  height: 100%;
  width: 100%;
  min-width: 200px;
  min-height: 200px;
  max-width: 100%;
}



